.form {
  margin-bottom: 30px;
  &__item {
    width: 415px;
    height: 50px;
    box-sizing: border-box;
    background: #f7f8ff;
    border: 1px solid #fff;
    border-radius: 8px;
    font-size: 14px;
    padding: 17px;
    margin-bottom: 15px;

    &--text_area {
      height: 145px;
      resize: none ;
    }
  }
}
